.BreadBottom {
  height: 6rem;
  width: 36rem;
  background: linear-gradient(#f08e4a, #e27b36);
  border-radius: 0 0 30px 30px;
  margin: 0 auto;
  box-shadow: 6px 6px 10px rgba(0,0,0,.5);
}

.BreadTop {
  height: 8rem;
  width: 36rem;
  background: linear-gradient(#bc581e, #e27b36);
  border-radius: 50% 50% 0 0;
  margin: 0 auto;
  position: relative;
  box-shadow: 6px 0 10px rgba(0,0,0,.5);
}

.Seeds1 {
  width: 5%;
  height: 5%;
  position: absolute;
  background-color: white;
  left: 30%;
  top: 50%;
  border-radius: 40%;
  transform: rotate(-20deg);
  box-shadow: inset -2px -3px #c9c9c9;
}

.Seeds1:after {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: white;
  left: -170%;
  top: -260%;
  border-radius: 40%;
  transform: rotate(60deg);
  box-shadow: inset -1px 2px #c9c9c9;
}

.Seeds1:before {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: white;
  left: 180%;
  top: -50%;
  border-radius: 40%;
  transform: rotate(60deg);
  box-shadow: inset -1px -3px #c9c9c9;
}

.Seeds2 {
  width: 5%;
  height: 5%;
  position: absolute;
  background-color: white;
  left: 64%;
  top: 50%;
  border-radius: 40%;
  transform: rotate(10deg);
  box-shadow: inset -3px 0 #c9c9c9;
}

.Seeds2:before {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: white;
  left: 150%;
  top: -130%;
  border-radius: 40%;
  transform: rotate(90deg);
  box-shadow: inset 1px 6px #c9c9c9;
}

.Tomato {
  width: 34rem;
  height: 5%;
  margin: 0 auto;
  position: relative;

  &::before {
    content: '';
    width: 49%;
    display: block;
    position: absolute;
    left: 0;
    background: linear-gradient(#ec1912, #f16209);
    border-radius: 2px;
    height: 100%;
  }

  &::after {
    content: '';
    height: 100%;
    width: 49%;
    display: block;
    position: absolute;
    right: 0;
    background: linear-gradient(#ec1912, #f16209);
    border-radius: 2px;
    box-shadow: 6px 0px 10px rgba(0,0,0,.5);
  }
}

.Patty {
  width: 36rem;
  height: 4rem;
  max-height: 30%;
  background: linear-gradient(#7f3608, #702e05);
  margin: 0 auto;
  border-radius: 15px;
  box-shadow: 6px 0 10px rgba(0,0,0,.5);
}

.Cheese {
  width: 40rem;
  height: 2%;
  margin: 0 auto;
  background: #f0e149;
  border-radius: 20px;
  box-shadow: 6px 0px 10px rgba(0,0,0,.5);
}

.Lettuce {
  width: 42rem;
  height: 2%;
  margin: 0 auto;
  background: linear-gradient(#228c1d, #91ce50);
  border-radius: 20px;
  box-shadow: 6px 0px 10px rgba(0,0,0,.5);
}

.Onion {
  width: 12rem;
  height: 3%;
  margin: 0 auto;
  border-radius: 2px;
  position: relative;
  background: linear-gradient(#f1b7b5, #ebd0bf);
  

  &::before {
    content: '';
    width: 97%;
    display: block;
    position: absolute;
    left: -100%;
    background: linear-gradient(#f1b7b5, #ebd0bf);
    border-radius: 2px;
    height: 100%;
  }

  &::after {
    content: '';
    width: 97%;
    display: block;
    position: absolute;
    right: -100%;
    background: linear-gradient(#f1b7b5, #ebd0bf);
    height: 100%;
    border-radius: 2px;
    box-shadow: 6px 0px 6px rgba(0,0,0,.5);
  }
}
